<template>
    <div>
        <top-nav title="商品模型"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 商品模型是用来规定某一类商品共有规格和属性的集合，其中规格会影响商品价格，同一个商品不同的规格价格会不同，而属性仅仅是商品的属性特质展示
            </p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form label-width="80px" size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="模型名称" label-width="100">
                        <el-input v-model="searchForm.title"></el-input>
                    </el-form-item>
                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                        <a href="javascript:void(0);" @click="searchForm.title = '',getList(1)">取消搜索</a>
                        <el-button size="mini" @click="creatEvent" type="danger">新增模型名称</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column    prop="type_id" label="ID" ></el-table-column>
                    <el-table-column    prop="name" label="模型名称" ></el-table-column>
                    <el-table-column label="操作"  >
                        <template slot-scope="scope">
                            <router-link :to="'/product/attr/index/' + scope.row.type_id" style="color: #ffffff;margin-right: 20px"  ><el-button size="mini" type="text"  icon="el-icon-search">属性列表</el-button></router-link>
                            <router-link :to="'/product/spec/index/' + scope.row.type_id" style="color: #ffffff;margin-right: 20px"><el-button size="mini"  type="text" icon="el-icon-search">规格列表</el-button></router-link>
                            <el-button size="mini" @click="editDataEvent(scope.row)" type="text">编辑</el-button>
                            <el-popover style="margin: 0 20px;" placement="top" width="160" v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="del(scope.row)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" size="mini"  slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <br/>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

        <!--编辑新增 弹窗-->
        <el-dialog title="商品模型" v-loading="subLoading" :visible.sync="dialog" width="500px" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small" label-width="80px">
                <el-form-item label="模型名称" >
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="creatData(true)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'ShippingIndex',
        components: {TopNav},
        data() {
            return {
                //修改 添加 弹窗  菊花
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //数据
                form:this.initForm(),
                //修改 添加 状态控制
                dialog: false,
                isUpdate:false,
                //搜索 form
                searchForm: {
                    title: '',  // 物流公司标题
                },

            }
        },
        created() {

            this.getList();

        },
        methods: {
            //初始化form数据
            initForm(){
                return {
                    name:'', //名称
                }
            },
            //新增物流模板事件
            creatEvent(){
                this.isUpdate = false;
                this.form =  this.initForm();
                this.dialog = true;
            },

            //修改物流模板
            editDataEvent(row){
                this.isUpdate = true;
                this.form =  row;
                this.dialog = true;
            },

            //新增物流
            creatData(){
                this.subLoading = true;
                let act = this.isUpdate ? 'edit' : 'create';
                api.post("/shop/admin.goodstype/" +act ,{
                    appToken: this.$store.getters.appToken,
                    name:this.form.name,
                    type_id:this.form.type_id,
                }).then(res => {
                    this.subLoading = false;
                    this.form = this.initForm();
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success'
                    });
                    this.dialog = false;
                    this.getList();
                }).catch((error) => {
                    this.subLoading = false;
                });
            },

            //获取物流公司
            getList(page = 1){
                this.loading =true;
                api.post("/shop/admin.goodstype/index", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    name:this.searchForm.title,
                }).then(res => {
                    this.loading =false;
                    this.datas = res;
                }).catch((error) => {
                    this.loading =false;
                });
            },


            //删除快递公司
            del(row) {
                this.loading = true;
                api.post("/shop/admin.goodstype/del", {
                    appToken: this.$store.getters.appToken,
                    type_id:row.type_id
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            },

        }
    }
</script>

